export default {
	data() {
		return {
			lists: [{
					id: 'view',
					name: '视图容器',
					open: false,
					pages: [
						'view',
						'scroll-view',
						'swiper'

						,
						'movable-view',
						'cover-view'

					]
				}, {
					id: 'content',
					name: '基础内容',
					open: false,
					pages: ['text', 'rich-text', 'progress']
				}, {
					id: 'form',
					name: '表单组件',
					open: false,
					pages: ['button', 'checkbox', 'form', 'input', 'label', 'picker', 'picker-view', 'radio',
						'slider',
						'switch', 'textarea'
					]
				}, {
					id: 'nav',
					name: '导航',
					open: false,
					pages: ['navigator']
				}, {
					id: 'media',
					name: '媒体组件',
					open: false,
					pages: [
						'image',

						'video',


						'audio',

					],
				},

				{
					id: 'map',
					name: '地图',
					open: false,
					pages: ['map']

				},
















				{
					id: 'web-view',
					name: '网页',
					open: false,
					pages: ['web-view']
				},

			]
		}
	},
	onShareAppMessage() {
		return {
			title: '欢迎体验uni-app',
			path: '/pages/tabBar/component/component'
		}
	},
	onNavigationBarButtonTap(e) {
		uni.navigateTo({
			url: '/pages/about/about'
		});
	},
	methods: {
		triggerCollapse(e) {
			if (!this.lists[e].pages) {
				this.goDetailPage(this.lists[e].url);
				return;
			}
			for (var i = 0; i < this.lists.length; ++i) {
				if (e === i) {
					this.lists[i].open = !this.lists[e].open;
				} else {
					this.lists[i].open = false;
				}
			}
		},
		goDetailPage(e) {
			if (typeof e === 'string') {
				uni.navigateTo({
					url: '/pages/component/' + e + '/' + e
				})
			} else {
				uni.navigateTo({
					url: e.url
				})
			}
		},
		outBtn: function(){
			uni.showModal({
				title: '清除launchFlag值',
				content: '确定要清除launchFlag值，进行重启测试？',
				success: function (res) {
					if (res.confirm) {
						console.log('用户点击确定');
						// 清除缓存
						uni.clearStorage();
						// 两秒后重启











						// 两秒后跳转

						uni.showToast({
							icon: 'none',
							duration:3000,
							title: '清除成功2秒后刷新'
						});
						setTimeout(function() {
							uni.navigateTo({
								url: '/pages/index/index'
							});
						}, 2000);
						

					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			});
		}
	}
}
